.BooksList {
    background-color: #ffffff09;
    display: flex; /* Align items horizontally */
    overflow-x: auto; /* Allow horizontal scrolling if items overflow */
    gap: 20px; /* Space between items */
    padding: 20px;
    border-radius: 20px;
}

.BookShelf{
    padding: 100px;
    background-color: #1B2027;
}

.Book {
    display: flex;
    flex-direction: row; /* Arrange the name and image side by side */
    align-items: center; /* Align content vertically */
    text-align: center;
}

.BookImage {
    height: 200px; /* Set the fixed height */
    width: auto; /* Adjust width while maintaining aspect ratio */
    object-fit: cover; /* Ensures the image fills the area while maintaining its aspect ratio */
    display: block; /* Ensures the image behaves like a block element */
}

.BookName{
    writing-mode: vertical-lr; /* Rotate text to vertical */
    text-orientation: mixed; /* Ensure text orientation is upright */
    font-family: var(--font-accent); /* Use the accent font for styling */
    font-size: 1rem; /* Adjust font size */
    font-weight: bold;
    margin: 0px;
    padding: 0px;
    color: white;
    background-color: rgba(255, 255, 255, 0.2); /* Add a slight background */
    border-radius: 10px; /* Round the edges */
}

.CategoryTitle{
    color: white;
}